<template>
  <div class="app-container">
    <!-- 头部 -->
    <div class="details">
      <div class="back" @click="back">&lt; 会员详情</div>
      <div class="detailsCon">
        <ul>
          <li>帐号：{{details.telephone}}</li>
          <li>昵称：{{details.reviewer}}</li>
          <li>姓名：{{details.author}}</li>
          <li>性别：{{sex}}</li>
          <li>邮箱：{{details.telephone}}</li>
          <li>积分：{{details.pageviews}}</li>
          <li>标签：</li>
          <li>地址：{{details.address}}</li>
          <li>注册时间：{{details.display_time}}</li>
        </ul>
      </div>
      <div class="detailsImg">
        <img :src="details.image_uri" alt="">
      </div>
    </div>
    <!-- 详情导航 -->
    <div class="detailsSub">
      <el-tag effect="plain" :class="{active:Mycar==typeActive}" size="medium" @click="menuChange('Mycar')">我的座驾</el-tag>
      <el-tag effect="plain" :class="{active:Myactive==typeActive}" size="medium" @click="menuChange('Myactive')">我的活动</el-tag>
      <el-tag effect="plain" :class="{active:MyIntegral==typeActive}" size="medium" @click="menuChange('MyIntegral')">我的积分</el-tag>
      <el-tag effect="plain" :class="{active:MyCollection==typeActive}" size="medium" @click="menuChange('MyCollection')">资讯收藏</el-tag>
      <el-tag effect="plain" :class="{active:MyAttention==typeActive}" size="medium" @click="menuChange('MyAttention')">关注车型</el-tag>
      <div :is="currentView"></div>
    </div>
  </div>
</template>

<script>

import Mycar from '@/views/member/MyCar'
import Myactive from '@/views/member/Myactive'
import MyIntegral from '@/views/member/MyIntegral'
import MyCollection from '@/views/member/MyCollection'
import MyAttention from '@/views/member/MyAttention'

export default {
  name: 'DetailsNav',
  components: {
    Mycar,
    Myactive,
    MyIntegral,
    MyCollection,
    MyAttention
  },
  data() {
    return {
      details: '',
      sex: '',
      currentView: 'Mycar',
      typeActive: 'Mycar',
      Mycar: 'Mycar',
      Myactive: 'Myactive',
      MyIntegral: 'MyIntegral',
      MyCollection: 'MyCollection',
      MyAttention: 'MyAttention'
    }
  },
  created() {
    console.log(this.$router)
    const row = JSON.parse(this.$router.currentRoute.query.data)
    this.details = row
    if (row.sex === 1) {
      this.sex = '男'
    } else {
      this.sex = '女'
    }
    console.log(this.details)
  },
  methods: {
    menuChange(menu) {
      this.currentView = menu
      console.log(this.currentView)
      this.typeActive = menu
    },
    back() {
      this.$router.back()
    }
  }
}
</script>
<style lang="scss">
.active {
        background-color: #409eff;
          color: #fff;
      }
  .app-container{
    .details{
      .back{
        display: block;
      }
      .detailsCon{
        width: 50%;
        display: inline-block;
        ul{
          li{
            line-height: 32px;
            &:nth-child(2),&:nth-child(3),&:nth-child(4),&:nth-child(5){
              display: inline-block;
              width: 50%;
            }
          }
        }
      }
      .detailsImg{
        height: 200px;
        display: inline-block;
        position: relative;
        img{
          width: 200px;
          position: absolute;
          top: 0;
        }
      }
    }
    .detailsSub{
      .el-tag{
        margin-right: 10px;
        // &:active{
        //   background: #409eff;
        //   color: #fff;
        // }
      }
    }
  }
</style>
